  fill: var(--red-text);
}

.poprigun {
  width: 80px;
  position: absolute;
  top: 213px;
  left: 100px;
  transform: translate(50%);
}

.ball {
  width: 7px;
  height: 7px;
  border-radius: 50%;
  animation: ball .5s infinite alternate;
  position: absolute;
  left: 15%;
}

@keyframes ball {
  0% {
    height: 5px;
    transform: scaleX(1, 7);
    background-color: #5f921c;
    top: 35px;
  }

  50% {
    height: 5px;
    background-color: #f8b456;
    transform: scaleX(1);
  }

  100% {
    background-color: #db5375;
    top: 0;
  }
}

.ball:nth-child(2) {
  animation-delay: .2s;
  left: 45%;
}

.ball:nth-child(3) {
  animation-delay: .3s;
  left: auto;
  right: 15%;
}

